<template>
    <div class="signin">
        <form class="box" autocomplete="smartystreets">
            <div class="logo">
                <img src="../../assets/image/logo.png" alt="">
            </div>
            <div class="input-field">
                <input type="text" autocomplete="smartystreets" required v-model.trim="form.name">
                <span class="highlight"></span>
                <span class="bar"></span>
                <label>用户名</label>
            </div>
            <div class="input-field password">
                <input type="password" autocomplete="smartystreets" required v-model.trim="form.password" @keyup.enter="loginAdmin">
                <span class="highlight"></span>
                <span class="bar"></span>
                <label>密码</label>
            </div>
            <div>
                <Button class="btn-signin" @click="loginAdmin">登 录</Button>
            </div>
        </form>
    </div>
</template>
<style lang="scss" scoped>
    @import '../../assets/scss/core.scss';
    @import './login.scss';
</style>

<script>
    import Cookies from 'js-cookie';
    export default {
        data() {
            return {
                form: {
                    name: '',
                    password: ''
                }
            }
        },
        methods: {
            loginAdmin() {
                this.judgyInfo()
            },
            async judgyInfo() {
                var patrn = /^(\w){6,20}$/;
                if (!patrn.exec(this.form.password)) {
                    this.$Message.error('请输入由6-20个字母、数字、下划线组成的密码...')
                    return
                }
                var patrn = /^([a-zA-Z0-9]|[\u4E00-\u9FA5]|[._]){2,20}$/
                if (!patrn.exec(this.form.name)) {
                    this.$Message.error('请输入2-20位由字母、汉字、数字、“_”、“.”的用户名')
                    return;
                }
                const res = await this.$ajaxPost(this.projectName + '/account/login.do', this.form)
                // console.log(res)
                this.$Message.success('恭喜您登录成功！')
                Cookies.set('Client-User', res.name)
                Cookies.set('Uu-id', res.id)
                this.$router.replace('/')
            }
        }
    }
</script>



